import React, {Component} from 'react';
import smart from '../../assets/interns.png'
// import './register-page-company.style.scss'
import {Col, Row, Tabs} from "antd";
import CompanyFormComponent from "../../components/Company/company-form/company-form.component";
import ContactPersonFormComponent from "../../components/Company/contact-person-form/contact-person-form.component";
const {TabPane} = Tabs;
class RegisterPageCompanyComponent extends Component {
    constructor(props) {
        super(props);
        this.state = {
            AKey : "1"
        }
    }
    changeT = (a)=>{
        // alert('hye')
        // console.log(a)
        this.setState({AKey:a}, ()=>{console.log(this.state.AKey)})
    }
    render() {
        return (
            <React.Fragment>
                <div className="register-fresher">
                    <div className="w-100 text-center mt-5">
                        <img src = {smart} width="100px"/>
                        <Row className = "mt-4">
                            <Col md = {6} xs = {0}>
                            </Col>
                            <Col xs = {24} md = {12}>
                                <p>Fill up the necessary details to continue</p>
                            </Col>
                            <Col xs = {24} md = {6}>
                                <p>Already registered? Login</p>
                            </Col>
                        </Row>
                    </div>
                    <div className="text-center">
                        <Tabs activeKey={this.state.AKey}>
                            <TabPane tab = "Company" key="1">
                                <div className="text-left forms">
                                    <CompanyFormComponent fun = {this.changeT}/>
                                </div>
                            </TabPane>
                            <TabPane tab = "Contact Person" key = "2">
                                <div className="text-left forms">
                                    <ContactPersonFormComponent fun = {this.changeT}/>
                                </div>
                            </TabPane>
                        </Tabs>
                    </div>
                </div>
            </React.Fragment>
        );
    }
}

export default RegisterPageCompanyComponent;
