import React from "react";
import './fresh-prof.style.scss'
const FreshProfComponent = ({title, img, details,classname}) => {
    return(
        <div className={classname}>
            <p>{title}</p>
            <div className="image">
                <img height="100px" width="100px" src = {img}/>
            </div>
            <p className="detail mt-2">{details}</p>
        </div>
    )
}
export default FreshProfComponent;