import React from "react";
import './job-listing-card.scss'
import UploadApplyButtonComponent from "../../COMMON/upload-and-apply-button/upload-apply-button.component";
import { message, Button} from "antd";
import {Link} from "react-router-dom";


const JobListingCardComponent = ({job,url,history}) =>{
    console.log("history",history)
    const handleClick = () =>{

    }
    return(
        <div className="job-listing-card w-100">
            <Link to = {`${url}/${job.job_id}`}>
            <h6 className="w-100 font-weight-bold"><span>{job.job_title}</span><span className="float-right">{job.experience_requires} yrs</span></h6>
            <p>{job.location}</p>
            <p>Description:</p>
            <p className="details">{job.description}</p>
            </Link>
            <div className="d-flex">
                <div className="d-flex align-items-center">
                <img className="image-circled mr-2" src = "https://s.pngix.com/pngfile/s/209-2094957_real-estate-background-png-real-estate-hd-logos.png" alt = "img" width="40px"/><h6>{job.company_name}</h6>
                </div>
                <div className="ml-auto">
                    <UploadApplyButtonComponent type="Apply"/>
                </div>
            </div>

        </div>
    )
}
export default JobListingCardComponent;