import React, {Component} from 'react';
import {Col, Row} from "antd";
import AboutCardComponent from "../about-card/about.-card.component";
import './about.style.scss'
import { LoremIpsum} from 'react-lorem-ipsum'
class AboutComponent extends Component {
    render() {
        return (
            <React.Fragment>
                <div className="about d-flex align-items-center justify-content-center">
                    <div>
                    <Row>
                        <Col className = "about-text" xs = {24} md = {16}>
                            <div className="w-100 pt-5">
                            <h1 className="w-100">Find A Perfect Career Path That Suits You</h1>
                            <div className="mt-3">
                                <p>We Are Looking For A Qualified Content Manager To Join Our Team. You Will Be Responsible For Creating, Improving And Maintaining Content To Achieve Our Business Goals. Your Duties Will Also Include Sharing Content To Raise Brand Awareness And Monitoring Web Traffic And Metrics To Identify Best Practices</p>
                            </div>
                                <div className="mt-5">
                                    <p>Our Ideal Candidate Is An Experienced Professional With Demonstrable Creative Writing Skills.  Raise Brand Awareness And Monitoring Web Traffic And Metrics To Identify Best Practices.</p>
                                </div>
                            </div>

                        </Col>
                        <Col xs = {24} md = {8}>
                            <Row>
                                <Col xs = {12} className ="d-flex justify-content-center">
                                    <AboutCardComponent details="Find Internships Related To Your Field And Make Way For A Successful Career." title="Find Internships" color="purple"/>
                                </Col>
                                <Col xs = {12} className ="d-flex justify-content-center">
                                    <AboutCardComponent details="Find A Career That Suits You And Work The Way You Love" title="Find Jobs" color="orange"/>
                                </Col>
                                <Col xs = {12} className ="d-flex justify-content-center">
                                    <AboutCardComponent details="If You Are A Recruiter And Looking For Perfect Candidates, Post A Job And Filter Out The Perfect Match." title="Post Jobs" color="green"/>

                                </Col>
                                <Col xs = {12} className ="d-flex justify-content-center">
                                    <AboutCardComponent details="Find A Career That Suits You And Work The Way You Love" title="Find Jobs" color="blue"/>

                                </Col>
                            </Row>
                        </Col>
                    </Row>
                    </div>
                </div>
            </React.Fragment>
        );
    }
}

export default AboutComponent;