import React, {Component} from 'react';
import './landing-page-search.style.scss'
import {Col, Row} from "antd";
import image from '../../../assets/pablo-sign-in.png'
import LandingPageFormComponent from "../landing-page-form/landing-page-form.component";
import {Image} from "react-bootstrap";

class LandingPageSearchComponent extends Component {
    render() {
        return (
            <React.Fragment>
                <div className="d-block">
                <div className="landing-page-main d-flex justify-content-center align-items-center">
                    <div className="first d-block w-100">
                    <Row type = "flex">
                    <Col xs = {{ span: 24, order: 2}} md = {{ span: 10, order: 1}}>
                        <div className="landing-page-search h-100">
                            <div className="d-block w-100">
                                <p>Looking For A Perfect Job?</p>
                                <h2>We've Got Your Back!</h2>
                                <LandingPageFormComponent/>
                            </div>
                        </div>
                    </Col>
                    <Col xs = {{span: 24, order: 1}} md = {{ span: 14, order: 2}}>
                        <div className="image-cont d-flex justify-content-center">
                            <img className="landing-image img-fluid" src = {image}/>
                        </div>
                    </Col>
                    </Row>
                    </div>
                </div>
                </div>

            </React.Fragment>
        );
    }
}

export default LandingPageSearchComponent;