import React, {Component} from 'react';
import './job-application-table.style.scss'

const tablerow =
    <tr>
        <td>ShaiLesh</td>
        <td>12-8-19</td>
        <td>React Developer</td>
        <td>close</td>
        <td>22-12-19</td>
        <td>22-12-19</td>
        <td><button className="applicant-select">Select</button><button className="applicant-reject">Reject</button></td>
    </tr>
class JobApplicationTableComponent extends Component {
    render() {
        return (
            <>
                <div className="job-application-table w-100">
                    <table className="w-100">
                        <tr className="heading">
                            <th>Name</th>
                            <th>Date</th>
                            <th>Position Applied</th>
                            <th>Status</th>
                            <th>Last Date to Apply</th>
                            <th>Access Application Till</th>
                            <th>Actions</th>
                        </tr>
                        {tablerow}
                        {tablerow}



                    </table>
                </div>

            </>
        );
    }
}

export default JobApplicationTableComponent;