import React from "react";
import './footer-list.style.scss'
const array = []
const FooterListcomponent = ({title, list = array}) => {
    return(
        <div className="footer-list">
            <h5>{title}</h5>
            <ul className="list-unstyled">
                {list.map((x) =>
                <li><p>{x}</p></li>)}
            </ul>
        </div>
    )
}
export default FooterListcomponent;