import React from "react";
import './profile-page.style.scss'
import {Col, Row} from "antd";
import ProfileUserTitleComponent from "../../../components/CANDIDATE/profile-user-title/profile-user-title.component";
import ProfileUploadResumeComponent from "../../../components/CANDIDATE/profile-upload-resume/profile-upload-resume.component";
import {Route, Link, Switch} from "react-router-dom";
import NavigationBarComponent from "../../../components/COMMON/navigation-bar/navigation-bar.component";
import FooterComponent from "../../../components/COMMON/footer/footer.component";
import ProfileUserDetailsComponent from "../../../components/CANDIDATE/profile-user-details/profile-user-details.component";
import JobListingCardComponent from "../../../components/CANDIDATE/job-listing-card/job-listing-card.component";
import ProfileAppliedSavedJobsComponent
    from "../../../components/CANDIDATE/profile-applied-saved-job/profile-applied-saved-jobs.component";
import ChatBoxComponent from "../../../components/COMMON/chatbox/chatbox.component";
import ProfileChatComponent from "../../../components/COMMON/profile-chat/profile-chat.component";

const ProfilePageComponent = (props) =>{
    return(
        <>
            {console.log('profile:', props.match.url)}
            <NavigationBarComponent/>
            <div className="profile-page">
                <Row>
                    <Col xs = {24} md = {6}>
                        <ProfileUserTitleComponent {...props}/>
                        <ProfileUploadResumeComponent/>
                    </Col>
                    <Col xs = {24} md = {18}>
                        <Switch>
                            <Route path = {`${props.match.url}/chat`} render = {(props)=> <ProfileChatComponent/>}/>
                            <Route path = {`${props.match.url}/applied`} render = {(props)=> <ProfileAppliedSavedJobsComponent title = "Applied Jobs"/>}/>
                            <Route path = {`${props.match.url}/saved`} render = {(props)=> <ProfileAppliedSavedJobsComponent title = "Saved Jobs"/>}/>
                            <Route path = {`${props.match.url}`} component = {ProfileUserDetailsComponent}/>
                        </Switch>

                    </Col>
                </Row>
            </div>
            <FooterComponent/>

        </>
    )
}
export default ProfilePageComponent;