import React from 'react';
import './upload-apply-button.style.scss'
import {message} from 'antd'
const hye = () => {
    alert('Applied Successfully')
}
const info = () =>{
    message.info('Successfully Applied')
}
const UploadApplyButtonComponent = ({type}) =>{
    return(<button onClick={hye} className="upload-apply">{type}</button>)
}
export default UploadApplyButtonComponent;