import React, {Component} from 'react';
import './job-preference-form.style.scss'
import {Checkbox, Upload} from "antd";
import InputFieldComponent from "../../COMMON/input field/input-field.component";
import MessageComponent from "../message/message.component";
import {Link} from "react-router-dom";
class JobPreferenceFormComponent extends Component {
    render() {
        return (
            <React.Fragment>
                <div className="preference">
                    <div className="text-center">
                        <Upload>
                            <div className= "upload"></div>
                        </Upload>
                    </div>
                    <InputFieldComponent place="About You"/>
                    <MessageComponent message="max 250 letters"/>
                    <InputFieldComponent place="Location"/>
                    <MessageComponent message="Upto five locations"/>
                    <div>
                        <select className="w-100">
                            <option selected disabled>Industry</option>
                            <option>option</option>
                        </select>
                    </div>
                    <MessageComponent message="eg: IT, BPO, education"/>
                    <div>
                        <select className="w-100">
                            <option selected disabled>Field</option>
                            <option>option</option>
                        </select>
                    </div>
                    <MessageComponent message="eg: Sales, HR, IT, Marketing"/>
                    <div>
                        <select className="w-100">
                            <option selected disabled>Role</option>
                            <option>option</option>
                        </select>
                    </div>
                    <div className="mt-3">
                        <p>Employement Preference</p>
                        <div className="d-flex justify-content-between">
                            <Checkbox>Full time</Checkbox>
                            <Checkbox>Part time</Checkbox>
                            <Checkbox>Internship</Checkbox>
                        </div>
                    </div>
                    <div className='d-flex justify-content-between mt-4'>
                        <h6 className="font-weight-bold">Skills</h6>
                        <p className="green-pref">+ Add skill</p>
                    </div>
                    <InputFieldComponent/>
                    {/*//add selected*/}

                    <div className='d-flex justify-content-between mt-4'>
                        <h6 className="font-weight-bold">Language</h6>
                        <p className="green-pref">+ Add language</p>
                    </div>
                    <InputFieldComponent/>
                    <div className="d-flex justify-content-between">
                        <Checkbox>Read</Checkbox>
                        <Checkbox>Write</Checkbox>
                        <Checkbox>Speak</Checkbox>
                    </div>
                    <div className="mt-3">
                        <select className="w-100">
                            <option selected disabled>Marital status</option>
                            <option></option>
                        </select>
                    </div>
                    <div className="mt-3"><Checkbox>I'm specially abled</Checkbox></div>
                    <div className="text-center mt-5">
                        <Link to = "/profile"><button>Save</button></Link>
                    </div>



                </div>
            </React.Fragment>
        );
    }
}
export default JobPreferenceFormComponent;