import './profile-user-details.style.scss'
import React, {Component} from 'react';
import {Icon} from "antd";
import edit from '../../../assets/Icon material-edit.svg'
import del from '../../../assets/Icon material-delete.svg'

const edit_tag = () =>{
    return(
        <img className="edit_tag" src = {edit}/>
    )
}

class ProfileUserDetailsComponent extends Component {
    render() {
        return (
            <React.Fragment>
                <div className="profile-user-details">
                    <h6 style={{fontWeight: "600"}}>Profile</h6>

                    <div className="d-flex mt-4">
                        <h6 style={{fontWeight: "600"}}>About You</h6><span className="ml-auto green-profile">{edit_tag()}Edit</span>
                    </div>
                    <div>
                        <p>Web app developer at USD technologies having 4 months of experience currently living in Bangalore</p>
                    </div>
                    <div className="d-flex mt-4">
                        <h6 style={{fontWeight: "600"}}>Skills</h6><span className="ml-auto green-profile">+ Add skill</span>
                    </div>
                    <div className="d-flex">
                        <p className="w-25">React JS</p><p className="w-25">2 years 2 months</p><img className="ml-auto" src = {del} height="17px"/>
                    </div>
                    <div className="d-flex mt-4">
                        <h6 style={{fontWeight: "600"}}>Work Experience</h6><span className="ml-auto green-profile">+ Add more</span>
                    </div>
                    <div className="mt-2">
                        <h6>React JS Developer<span style={{fontSize: "70%"}} className="ml-5 green-profile">{edit_tag()}Edit</span></h6>
                        <p>FAME Technologies</p>
                        <p>1 year 6 months</p>
                        <p>3.3 LPA</p>
                    </div>

                    <div className="mt-2">
                        <h6>Angular Developer<span style={{fontSize: "70%"}} className="ml-5 green-profile">{edit_tag()}Edit</span></h6>
                        <p>FAME Technologies</p>
                        <p>1 year 6 months</p>
                        <p>3.3 LPA</p>
                    </div>
                    <div className="d-flex mt-4">
                        <h6 style={{fontWeight: "600"}}>Educational details</h6><span className="ml-auto green-profile">{edit_tag()}Edit</span>
                    </div>
                    <div className="mt-3">
                        <h6>Bachelor of Engineering<span style={{fontSize: "70%"}} className="ml-5 green-profile">{edit_tag()}Edit</span></h6>
                        <p>Bangalore University</p>
                        <p>Full time - 2018</p>
                    </div>
                    <div className="mt-3">
                        <h6>Bachelor of Engineering<span style={{fontSize: "70%"}} className="ml-5 green-profile">{edit_tag()}Edit</span></h6>
                        <p>Bangalore University</p>
                        <p>Full time - 2018</p>
                    </div>
                    <div className="d-flex mt-4">
                        <h6 style={{fontWeight: "600"}}>Certications</h6><span className="ml-auto green-profile">{edit_tag()}Edit</span>
                    </div>
                    <div className="mt-2">
                        <h6>NISM series V<span style={{fontSize: "70%"}} className="ml-5 green-profile">{edit_tag()}Edit</span></h6>
                        <p>Bangalore University</p>
                        <p>Valid till - October, 2045</p>
                    </div>
                    <div className="d-flex mt-4">
                        <h6 style={{fontWeight: "600"}}>Job preference</h6><span className="ml-auto green-profile">{edit_tag()}Edit</span>
                    </div>
                    <div className="w-100 d-inline-block mt-2">
                        <p className="w-50 float-left">Industry: Information Technology</p>
                        <p className="w-50 float-left">Preferred location: Goa</p>
                        <p className="w-50 float-left">Field: Information Technology</p>
                        <p className="w-50 float-left">Expected Salary: 6 LPA</p>
                        <p className="w-50 float-left">Role: Web App Developer</p>
                    </div>

                    <div className="d-flex mt-4">
                        <h6 style={{fontWeight: "600"}}>Personal details</h6><span className="ml-auto green-profile">{edit_tag()}Edit</span>
                    </div>
                    <div className="w-100 d-inline-block mt-2">
                        <p className="w-50 float-left">Marital status: Single</p>
                        <p className="w-50 float-left">Date of birth: 16 - 04 - 1997</p>
                        <p className="w-50 float-left">Gender: Male</p>
                        <p className="w-50 float-left">Nationality: Indian</p>
                        <p className="w-50 float-left">Address: 1300, 13th cross, Stage 2, Eshwara layout, Indiranagar, Bangalore</p>

                    </div>



                </div>
                
            </React.Fragment>
        );
    }
}

export default ProfileUserDetailsComponent;