import React from "react";
import './selected-company-joblist.style.scss'
import NavigationBarComponent from "../../../components/COMMON/navigation-bar/navigation-bar.component";
import {Breadcrumb, Col, Row} from "antd";
import AboutRecruiterComponent from "../../../components/CANDIDATE/about-recruiter/about-recruiter.component";
import SimilarJobsLocationComponent from "../../../components/CANDIDATE/similar-jobs-location/similar-jobs-location.component";
import RecruiterJobListCardComponent from "../../../components/CANDIDATE/recruiter-job-list-card/recruiter-job-list-card.component";
import FooterComponent from "../../../components/COMMON/footer/footer.component";
import {Link} from "react-router-dom";
const array = [1,2]
const jobs = ['web designer', 'Web application', 'Web developer', 'Core java', 'NodeJS developer']
const location = ['Bangalore', 'Chennai', 'Goa', 'Mumbai', 'Pune'];

const SelectedCompanyJoblistComponent = () => {
    return(
        <>
            <div>
                <NavigationBarComponent/>
                <div className="selected-company-joblist">
                    <Breadcrumb>
                        <Breadcrumb.Item>Home</Breadcrumb.Item>
                        <Breadcrumb.Item>Web Designer</Breadcrumb.Item>
                        <Breadcrumb.Item>Company Name</Breadcrumb.Item>
                        <Breadcrumb.Item>All Jobs</Breadcrumb.Item>
                    </Breadcrumb>
                    <Row type = "flex">
                        <Col xs = {{span : 24, order : 2}} md = {{span : 6, order: 1}}>
                            <AboutRecruiterComponent/>
                            <SimilarJobsLocationComponent title="Similar jobs for you" type={jobs}/>
                            <SimilarJobsLocationComponent title="Location" type={location}/>
                        </Col>
                        <Col xs = {{span : 24, order : 1}} md = {{span : 18, order: 1}}>
                            <div className="all-job-container">
                                {array.map(()=><Link to={`/search/description`}><RecruiterJobListCardComponent/></Link>)}
                            </div>
                        </Col>
                    </Row>
                </div>
                <FooterComponent/>
            </div>
        </>
    )
}
export default SelectedCompanyJoblistComponent;