import React from 'react'
import './footer.style.scss'
import {Col, Row} from "antd";
import FooterListcomponent from "../footer-list/footer-list.component";

const it_jobs = ['java', 'corel draw', 'HTML', 'React',"CSS", 'Bootstrap'];
const non_it_jobs = ['Company', 'Marketing', 'Designing'];
const job_seekers = ['job search', 'Find companies', 'Upload resume', 'Report Issue'];
const employees = ['Employer login-page', 'Job posting', 'Report a problem'];
const jobs_by_location = ['Goa', 'Bangalore', 'Pune', 'Delhi'];
const legal = ['Privacy policy', 'Terms of use', 'Complaints'];
const about_us = ['Home', 'Career with Smart Interns', 'Contact us'];


const FooterComponent = () =>{
    return(
        <div className="footer">
            <Row>
                <Col md = {6} xs = {24}>
                    <Row>
                        <Col xs = {12} md = {24}>
                            <FooterListcomponent title="IT Jobs" list={it_jobs}/>
                        </Col>
                        <Col xs = {12} md = {24}>
                            <FooterListcomponent title="Non-IT jobs" list={non_it_jobs}/>
                        </Col>
                    </Row>
                </Col>
                <Col md = {6} xs = {24}>
                    <Row>
                        <Col xs = {12} md = {24}>
                            <FooterListcomponent list={job_seekers} title="Job Seekers"/>
                        </Col>
                        <Col xs = {12} md = {24}>
                            <FooterListcomponent list={employees} title="Employers"/>
                        </Col>
                    </Row>
                </Col>
                <Col md = {6} xs = {24}>
                    <Row>
                        <Col xs = {12} md = {24}>
                            <FooterListcomponent list={jobs_by_location} title="Jobs By Location"/>
                        </Col>

                    </Row>
                </Col>
                <Col md = {6} xs = {24}>
                    <Row>
                        <Col xs = {12} md = {24}>
                            <FooterListcomponent list={about_us} title="About Us"/>
                        </Col>
                        <Col xs = {12} md = {24}>
                            <FooterListcomponent list={legal} title="Legal"/>
                        </Col>
                    </Row>
                </Col>
            </Row>
        </div>
    )
}

export default FooterComponent;