import React, {Component} from 'react';
import './profile-user-title.style.scss'
import {Link} from "react-router-dom";

class ProfileUserTitleComponent extends Component {
    render() {
        return (
            <React.Fragment>
                {console.log('user-title:', this.props.match.url)}
                <div className="user-profile-title text-center">
                    <img width="100px" height="100px" src = "https://i.pinimg.com/originals/d6/f5/1e/d6f51ec0f1361f009732b1c503e31b1d.jpg"/>
                    <Link to = {`${this.props.match.url}`}><h4 className="mt-2">Oliver Queen</h4></Link>
                    <p>Banglore</p>
                    <p>shaldankar37@gmail.com</p>
                    <p>7878789090</p>
                    <Link to={`${this.props.match.url}/saved`}><h6>Saved Jobs</h6></Link>
                    <Link to={`${this.props.match.url}/applied`}><h6>Applied Jobs</h6></Link>
                    <Link to={`${this.props.match.url}/chat`}><h6>Chat</h6></Link>
                </div>
            </React.Fragment>
        );
    }
}

export default ProfileUserTitleComponent;