import React, {Component} from 'react';
import {Checkbox} from "antd";
import './landing-page.-form.style.scss'
import briefcase from '../../../assets/Icon ionic-md-briefcase.svg'
import Counts from "../counts/counts.component";
import {Link} from "react-router-dom";
import SearchButtonComponent from "../search-button/search.button.component";
const place = () => <p><img src = {briefcase}/> Job Title</p>
class LandingPageFormComponent extends Component {

    state = {
        title : this.title,
        location: this.location
    }


    render() {
        return (
            <React.Fragment>
                <div className="w-100 landing-page-form d-block">
                    <input className="w-100" name = "title" onChange={(value) => this.title = value.currentTarget.value} type = "text" placeholder= "Job Title"/>
                    <div className="d-flex mt-4">
                        <input type = "text" name = "location" onChange={(value) => this.location = value.currentTarget.value} placeholder="Location" className="w-100 mr-2"/>
                        <div className="ml-auto">
                            <Link to={{ pathname: '/search', title: this.title, location: this.location}}>
                                <button className="search-button">Search</button>
                            </Link>
                        </div>
                    </div>
                    <div className="mt-3">
                        <Checkbox>Internship</Checkbox>
                        <Checkbox>Full Time</Checkbox>
                    </div>
                    <div className="d-flex justify-content-between w-100 mt-5">
                        <Counts type= "Job Posted" num = {5900}/>
                        <Counts type= "Job Seekers" num = {2100}/>
                        <Counts type= "Recruiters" num={67}/>

                    </div>
                </div>
            </React.Fragment>
        );
    }
}

export default LandingPageFormComponent;