import React, {useState} from "react";
import './chat-header-list.style.scss'
import ChatHeaderComponent from "../chat -header/chat-header.component";

const ChatHeaderListComponent = () =>{
    const [chosen, setChosen] = useState(1);
    const test = [1,2,3,4];
    return(
        <React.Fragment>
            <div className="profile-header-list">
                {test.map(t =>(
                    <ChatHeaderComponent company_name="Fametronix" post="React Developer" status="online" key ={t} count = {t} active = {t == chosen} onClick = {() => setChosen(t)}/>
                ))}
            </div>

        </React.Fragment>
    )
}
export default ChatHeaderListComponent