import React, {Component} from 'react';
import {Select, Input} from "antd";
import './job-search-bar.style.scss'

class JobSearchBarComponent extends Component {
    render() {
        return (
            <React.Fragment>
                <div className="d-flex w-100 job-search-bar justify-content-between">
                    <Input placeholder="Post" style = {{width : "49%"}}/>
                    <Input placeholder="location" style = {{width : "23%"}}/>
                    <div className="small w-25">
                    <select>
                        <option selected disabled>experience</option>
                        <option>ffvef</option>
                    </select>
                    </div>
                </div>

            </React.Fragment>
        );
    }
}

export default JobSearchBarComponent;