import React from "react";
import {Icon} from "antd";
import './recruiter-job-list-card.style.scss'
import ApplyButtonComponent from "../../COMMON/apply-button/apply-button.component";

const RecruiterJobListCardComponent = () =>{
    return(
        <div className="recruiter-card">
            <h6 className="w-100"><span>Web Designer</span><span className="float-right">0-1 yrs</span></h6>
            <p>Bangalore</p>
            <p>Qualifications:</p>
            <p className="details">The text-overflow property specifies how overflowed content that is not displayed should be signaled to the user. It can be clipped, display an ellipsis, or display a custom string, The text-overflow property specifies how overflowed content that is not displayed should be signaled to the user. It can be clipped, display an ellipsis, or display a custom string</p>
            <div className="d-flex">
                <div className="ml-md-auto">
                    <ApplyButtonComponent type="View details"/><ApplyButtonComponent type="Apply"/>
                </div>
            </div>

        </div>
    )
}
export default RecruiterJobListCardComponent;