import React, {Component} from 'react';
import smart from '../../../assets/interns.png'
import './register-page-fresher.style.scss'
import {Col, Row, Tabs} from "antd";
import EducationalFormcomponent from "../../../components/CANDIDATE/educational-form/educational-formcomponent";
import PersonalDetailsFormComponent from "../../../components/CANDIDATE/personal-details-form/personal-details-form.component";
const {TabPane} = Tabs;
class RegisterFresherComponent extends Component {
    render() {
        return (
            <React.Fragment>
                <div className="register-fresher">
                    <div className="w-100 text-center mt-5">
                        <img src = {smart} width="100px"/>
                        <Row className = "mt-4">
                            <Col md = {6} xs = {0}>
                            </Col>
                            <Col xs = {24} md = {12}>
                                <p>Fill up the necessary details to continue</p>
                            </Col>
                            <Col xs = {24} md = {6}>
                                <p>Already registered? Login</p>
                            </Col>
                        </Row>
                    </div>
                    <div className="text-center">
                        <Tabs defaultActiveKey="1">
                            <TabPane tab = "Personal" key="1">
                                <div className="text-left forms">
                                    <PersonalDetailsFormComponent/>
                                </div>
                            </TabPane>
                            <TabPane tab = "Educational" key = "2">
                                <div className="text-left forms">
                                    <EducationalFormcomponent/>
                                </div>
                            </TabPane>
                        </Tabs>
                    </div>
                </div>
            </React.Fragment>
        );
    }
}

export default RegisterFresherComponent;