import React, {Component} from 'react';
import './filter-job.style.scss'
import {Menu, Checkbox} from "antd";

const { SubMenu} = Menu;

class FilterJobComponent extends Component {
    render() {
        return (
            <React.Fragment>
                <div className="filter-job">
                    <Menu style={{width: 260}} mode = "inline">
                        <Menu.Item style={{borderBottom:  "1px solid #dadada"}}>Filter by:</Menu.Item>
                        <SubMenu key = "sub1" title="Experience">
                            <Menu.Item key = "1">less than 1</Menu.Item>
                            <Menu.Item key = "2">more than 1</Menu.Item>
                        </SubMenu>
                        <SubMenu key = "sub2" title="Company Type">
                            <Menu.Item key = "3">IT</Menu.Item>
                            <Menu.Item key = "4">Core Electronics</Menu.Item>
                        </SubMenu>
                        <SubMenu key = "sub3" title="Location">
                            <Menu.Item key = "5">Pune</Menu.Item>
                            <Menu.Item key = "6">Banglore</Menu.Item>
                        </SubMenu>
                        <SubMenu key = "sub4" title="Job Type">
                            <div className="pl-4 d-flex justify-content-between">
                                <Checkbox>Full Time</Checkbox>
                                <Checkbox>Part Time</Checkbox>
                            </div>

                        </SubMenu>
                        <SubMenu key = "sub5" title="Salary">
                            <Menu.Item key = "7">less than 3LPA</Menu.Item>
                            <Menu.Item key = "8">More than 3LPA</Menu.Item>
                        </SubMenu>
                    </Menu>


                </div>
            </React.Fragment>
        );
    }
}

export default FilterJobComponent;