import React, {Component} from 'react';
import person from '../../../assets/Icon awesome-user-alt.svg'
import mail from '../../../assets/Icon material-email.svg'
import phone from '../../../assets/Icon material-call.svg'

import './component-person-form.style.scss'
import InputFieldComponent from "../../COMMON/input field/input-field.component";
import {Checkbox} from "antd";

class ContactPersonFormComponent extends Component {
    changeT = ()=>{
        this.props.fun("1")
    }
    render() {
        return (
            <>
                <div className="contact-person">
                    <InputFieldComponent img={person} type="text" place="Contact Person's Name"/>
                    <InputFieldComponent img={mail} type="email" place="Official Mail Address"/>
                    <InputFieldComponent img={phone} type="number" place="Mobile Number"/>
                    <InputFieldComponent type="text" place="Position in the company"/>
                    <div className="contact-margin">
                        <Checkbox>I agree to the terms and conditions of the smartinterns</Checkbox>
                    </div>
                    <div className="contact-margin">
                        <button className="contact-person-button">Register</button>
                    </div>
                </div>

            </>
        );
    }
}

export default ContactPersonFormComponent;
