import React from "react";
import './job-preference.style.scss'
import logo from '../../../assets/interns.png'
import JobPreferenceFormComponent from "../job-preference-form/job-preference-form.component";

const JobPreference = () => {
    return(
        <div className="job_preference">
            <div className="text-center">
                <img className="mt-4" width="100px" src = {logo}/>
                <p className="mt-4">Fill up the necessary details to continue</p>
                <h5>Job preference</h5>
            </div>
            <div className="d-flex justify-content-center">
                <div className="pref_form d-block">
                    <JobPreferenceFormComponent/>
                </div>
            </div>

        </div>
    )
}
export default JobPreference;