import axios from 'axios'
import http from './http'

const companyApiEndpoint = "http://localhost:5000/api/GetCompany";
export function getCompany(id) {
    return http.get(companyApiEndpoint + "/" + id)
}

export function createCompany(company) {
    return http.post(companyApiEndpoint, company);
}

export function updateCompany(company) {
    return http.put(companyApiEndpoint, company);
}

export function deleteCompany(company_id){
    return http.delete(companyApiEndpoint, company_id)
}

export function searchCompanyByLocation(company_location) {
    return http.post(companyApiEndpoint, company_location)
}