import React from "react";
import './about-card.style.scss'
import img from '../../../assets/Icon ionic-md-briefcase.svg'
const AboutCardComponent = ({title, details, image,color}) => {
    return(

        <div className= {"about-card" +" "+ color}>
            {console.log('color:', color)}
            <img width="35px" alt = "img" src = {img}/>
            <p className="about-title mt-3">{title}</p>
            <p className="about-details">{details}</p>

        </div>
    )
}
export default AboutCardComponent;