import React, {Component} from 'react';
import InputFieldComponent from "../../COMMON/input field/input-field.component";
import './educational-form.style.scss'
import {Radio, DatePicker} from "antd";
const {MonthPicker, RangePicker, WeekPicker} = DatePicker
class EducationalFormcomponent extends Component {
    render() {
        return (
            <React.Fragment>
                <div className="educational">
                    <InputFieldComponent place="Highest Qualification"/>
                    <InputFieldComponent place="Specialization/ course"/>
                    <div className="w-100 mt-3">
                        <select className="w-100">
                            <option disabled selected>Institute name</option>
                            <option></option>
                        </select>
                    </div>
                    <div className="w-100 mt-3">
                        <select className="w-100">
                            <option disabled selected>Board of study</option>
                            <option></option>
                        </select>
                    </div>
                    <div className="mt-3">
                        <span className="mr-3">Course type</span>
                        <Radio.Group>
                            <Radio value="f-time">Full time</Radio>
                            <Radio value="p-time">Part time</Radio>
                            <Radio value="correspondence">Correspondance</Radio>
                        </Radio.Group>
                    </div>
                    <div className="mt-3">
                        <p>Period of Study</p>
                        <RangePicker />
                    </div>

                    <div className="mt-3 d-flex">
                        <div className="mt-3 d-block">
                        <span className="mr-3">Active Backlogs</span>
                        <Radio.Group>
                            <Radio value="yes">Yes</Radio>
                            <Radio value="no">No</Radio>
                        </Radio.Group>
                        </div>
                        <InputFieldComponent place="Number of Backlogs"/>
                    </div>
                    <p className="edu-blue">+ Add another education</p>
                    <div>
                        <button>Continue</button>
                    </div>
                </div>
            </React.Fragment>
        );
    }
}

export default EducationalFormcomponent;