import React, {Component} from 'react';
import {Col, Row} from "antd";
import './login.style.scss'
import login from '../../../assets/fogg-premium-upgrade-1.svg'
import si_logo from '../../../assets/interns.png'
import google from '../../../assets/search.svg'
import LoginButtonComponent from "../../COMMON/login-button/login-button.component'";
import {Link} from "react-router-dom";
class LoginComponent extends Component {
    render() {
        return (
            <React.Fragment>
                <Row className = "login">
                    <Col className = "h-100 pl-5 pr-5" xs = {0} md = {12}>
                        <div className="h-100 d-flex justify-content-center align-items-center">
                            <img className="w-100" src = {login}/>
                        </div>
                    </Col>
                    <Col className = "h-100 text-center d-flex align-items-center" xs = {24} md = {12}>
                        <div className="d-block ml-auto mr-auto">
                        <img className="mt-4" width="100px" src={si_logo}/>
                        <div className="d-flex align-items-center">
                            <div className="w-100 d-block text-center">
                                <h1 className="mt-3">Welcome to <span className="green">Brainy Interns</span></h1>
                                <h5 className="mb-5">As Smart As You Are</h5>
                                <div className="d-flex justify-content-center">
                                    <div className="text-center form mt-3">
                                        <h6>Let's Get Powered Up!</h6>
                                        <div className="field ml-auto mr-auto">
                                            <input type = "email" placeholder="Username or Phone Number"/>
                                        </div>
                                        <div className="field ml-auto mr-auto mt-4">
                                            <input type = "password" placeholder="Password"/>
                                        </div>
                                        <div className="text-right">
                                            <p className="ml-auto">forgot password?</p>
                                        </div>
                                        <Link to= "/">
                                            <LoginButtonComponent value="Login"/>
                                        </Link>
                                    </div>
                                </div>
                                <p className="mt-5">Or Sign in with</p>
                                <img src = {google} />
                                <p className="mt-2">Doesn't have an account? <Link to = "/Career_Stage">Register</Link></p>
                            </div>
                        </div>
                        </div>
                    </Col>
                </Row>
            </React.Fragment>
        );
    }
}

export default LoginComponent;