function CandidateRouting() {
    return (
        <Switch>
            <Route path = "/Job_Preference" component={JobPreferencePageComponent}/>
            <Route path = "/Career_Stage/Register" component = {RegisterPageComponent}/>
            <Route path = "/Career_Stage/Fresher" component = {RegisterFresherComponent}/>
            <Route path = "/Career_Stage" component = {CareerStagePageComponent}/>
            <Route path = "/login" component = {LoginComponentPage}/>
            <Route path = "/search/description/All_Jobs" component = {SelectedCompanyJoblistComponent}/>
            <Route path = "/search/:id" component = {SelectedCompanyDescription}/>
            <Route path = "/search" component = {JobListing}/>
            <Route path="/profile" component={ProfilePageComponent}/>
            <Route exact path = "/" component = {HomeComponent}/>
        </Switch>


);
}


