import React from "react";
import {useSpring, animated} from "react-spring";
import './counts.style.scss'
const Counts = ({type, num}) => {
 const props = useSpring({x: parseInt(num), from: {x: 1}})
 return(
     <div className="counts d-block text-center">
      <animated.span>{props.x.interpolate(val =>Math.floor(val))}</animated.span><span>+</span>
      <p>{type}</p>
     </div>
 )
 }

 export default Counts;