
import './company-title-card.style.scss'
import logo from '../../../assets/logo.svg'
import {Link} from "react-router-dom";
import React, {Component} from 'react';

class CompanyTitleCardComponent extends Component {
    componentDidMount() {
        // const {id} = this.props;
        console.log('hi',this.props)
    }


    render() {
        const {props, company_details} = this.props;
        return (
            <div className="company-title">
                <h6 style={{fontWeight: "600"}}>About Recruiter</h6>
                <div className="d-flex h-100 align-items-center" >
                    <img src = {company_details.logo} alt = "logo" height = "40px"/>&nbsp;&nbsp;&nbsp;&nbsp;<h5 className="mt-2">{company_details.name}</h5>
                </div>
                <p className="mt-2">Indira nagar, White field</p>
                <p className="">Company type: <span className="font-weight-lighter">Information technology, Product management</span></p>
                <p className="">Website: <span className="font-weight-lighter">{company_details.website}</span></p>
                <p className="">Company size: <span className="font-weight-lighter">{company_details.no_of_employees} employees</span></p>
                <p className="small-blue"><Link to = {`${props.match.url}/All_Jobs`}>View all openings in Alpha Media Services</Link></p>
            </div>
        );
    }
}

export default CompanyTitleCardComponent;
