import React from "react";
import logo from '../../../assets/logo.svg'
import './about-recruiter.style.scss'
const AboutRecruiterComponent = () => {
    return(
        <div className="about-recruiter">
            <h6>About Recruiter</h6>
            <div className="d-flex">
                <img src = {logo} width="40px" /><h5 className="mt-2 ml-2">Alpha Media Services</h5>
            </div>
            <p className="mt-2">Indiranagar, Domlur</p>
            <p>Company type: <span>Information technology, Project management, Product development</span></p>
            <p>Website: <span>www.alphamedia.com</span></p>
            <p>Company size: <span>100-150 employees</span></p>
        </div>
    )
}
export default AboutRecruiterComponent;