import React, {Component} from 'react';
import {Switch} from "antd";
import './hr-profile.style.scss'

class HrProfileComponent extends Component {
    render() {
        return (
            <>
                <div className="hr-profile">
                    <div className="d-flex justify-content-between">
                        <h6 className="font-weight-bold">HR Profile</h6><button className="hr-profile-button">+ Add New HR</button>
                    </div>
                    <div className="mt-3">
                        <table className="w-100">
                            <tr>
                                <th>Name</th>
                                <th>Mail ID</th>
                                <th>Contact Number</th>
                                <th>Company</th>
                                <th>Branch</th>
                                <th>Status</th>
                            </tr>
                            <tr>
                                <td>ShaiLesh</td>
                                <td>cbhcn@gmail.com</td>
                                <td>7878787878</td>
                                <td>Fame</td>
                                <td>Indira Nagar</td>
                                <td><Switch></Switch></td>
                            </tr>
                        </table>
                    </div>
                </div>

            </>
        );
    }
}

export default HrProfileComponent;