import React, {Component} from 'react';
import JobListingCardComponent from "../job-listing-card/job-listing-card.component";
import './profile-applied-saved-jobs.style.scss'
class ProfileAppliedSavedJobsComponent extends Component {
    render() {
        return (
            <React.Fragment>
                {console.log('y:', this.props.title)}
                <div className="applied-saved">
                    <h5 className="as-title">{this.props.title}</h5>
                    <JobListingCardComponent/>
                    <JobListingCardComponent/>
                </div>
            </React.Fragment>
        );
    }
}

export default ProfileAppliedSavedJobsComponent;