import React, {Component} from 'react';
import { Tabs } from 'antd'
import JobApplicationTableComponent from "../job-application-table/job-application-table.component";
import './recruiter-home-tab.style.scss'
const { TabPane } = Tabs;

class RecruiterHomeTabsComponent extends Component {
    render() {
        return (
            <>
                <div className="recruiter-tab d-flex">
                    <Tabs tabPosition="left">
                        <TabPane tab = "New(72)" key = "1">
                            <JobApplicationTableComponent/>
                        </TabPane>
                        <TabPane tab = "Shortlisted" key = "2">
                            <JobApplicationTableComponent/>
                        </TabPane>
                        <TabPane tab = "Rejected" key = "3">
                            <JobApplicationTableComponent/>
                        </TabPane>
                        <TabPane tab = "In Process" key = "4">
                            <JobApplicationTableComponent/>
                        </TabPane>
                        <TabPane tab = "Hired" key = "5">
                            <JobApplicationTableComponent/>
                        </TabPane>
                        <TabPane></TabPane>
                    </Tabs>
                </div>


            </>
        );
    }
}

export default RecruiterHomeTabsComponent;