import React, {Component} from 'react';
import './chatbox.style.scss'
import ScrollToBottom from 'react-scroll-to-bottom'
import aattach from  '../../../assets/Group.svg'
import sent from '../../../assets/sent-mail.svg'
const MessageFromOther = ({text}) => {
    return <div className="chat-other d-flex flex-column justify-content-start-s">
        <div className="d-flex flex-column">
            <div className="d-flex justify-content-start ml-2"><span>1:45 PM</span></div>
            <div className="chat-message-time-other">
            </div>
            <div className="chat-message-other">
                <p>{text}</p>
            </div>
        </div>
    </div>;
}

const MessageFromSelf =({text})=> {
    return <div className="chat-self d-flex justify-content-end">
        <div className="d-flex flex-column">
            <div className="d-flex justify-content-end mr-2"><span>1:45 PM</span></div>
        <div className="chat-message-time-self">

        </div>
        <div className="chat-message-self">
            <p>{text}</p>
        </div>
        </div>
    </div>;
}

class ChatBoxComponent extends Component {
    render() {
        return (
            <>
                <div className="chatbox">
                    <div className="chatbox-title">
                        <h6>ShaiLesh</h6>
                        <p>React App Developer</p>
                    </div>
                    <div className="chatbox-container" >
                        <div className="h-100 row align-items-end">
                            <ScrollToBottom className = "scroll-to-bottom">
                                <div className="p-2 w-100">
                                        <MessageFromOther text="Hello"/>
                                        <MessageFromSelf text="Hye hwbwfweggwegfewygyggfewgwehg"/>
                                        <MessageFromOther text = "Thank you for showing interest towards fame technologies.Send your resume as soon as possible to start with the screening process"/>
                                        <MessageFromSelf text = "Okay"/>
                                        <MessageFromSelf text="Hye hwbwfweggwegfewygyggfewgwehg"/>
                                        <MessageFromOther text = "Thank you for showing interest towards fame technologies.Send your resume as soon as possible to start with the screening process"/>
                                        <MessageFromSelf text = "Okay"/>
                                        <MessageFromSelf text="Hye hwbwfweggwegfewygyggfewgwehg"/>
                                        <MessageFromOther text = "Thank you for showing interest towards fame technologies.Send your resume as soon as possible to start with the screening process"/>
                                        <MessageFromSelf text = "Okay"/>
                                        <MessageFromOther text="Good Bye"/>
                                        <MessageFromSelf text= "Bye"/>
                                        <MessageFromSelf text= "Bye"/>
                                        <MessageFromOther text="Hello"/>
                                    </div>
                            </ScrollToBottom>
                        </div>
                    </div>
                    <div className= "chat-footer d-flex align-items-center">
                        <img src={aattach}/>
                        <textarea className="w-100" placeholder="attach files or type a message" rows="1">

                        </textarea>
                        <div className="chat-sent-button">
                            <img src = {sent}/>
                        </div>
                    </div>
                </div>

            </>
        );
    }
}

export default ChatBoxComponent;
