import React, {Component} from 'react';
import InputFieldComponent from "../../COMMON/input field/input-field.component";
import mail from "../../../assets/Icon material-email.svg";
import phone from "../../../assets/Icon material-call.svg";
import city from '../../../assets/Icon material-location-city.svg'
import calender from '../../../assets/Icon awesome-calendar-alt.svg'
import loc from '../../../assets/Icon material-location-on.svg';
import {Checkbox, DatePicker, Radio, Upload} from "antd";
import globe from '../../../assets/Icon ionic-ios-globe.svg'
import './company-form.style.scss'
import LookingForButtonComponent from "../../COMMON/looking-for-button/looking-for-button.component";
import MessageComponent from "../../CANDIDATE/message/message.component";



class CompanyFormComponent extends Component {
    changeTab = ()=>{
        this.props.fun("2")
    }
    render() {
        return (
            <>
                <div className="company-details-form">
                    <InputFieldComponent type="text" img={city} place="Company Name"/>
                    <InputFieldComponent type="text" img={loc} place="Company Address"/>
                    <select className="w-100">
                        <option selected disabled>Company Type</option>
                    </select>
                    <MessageComponent message="eg: IT, BPO"/>
                    <div className="mt-3">
                        <span className="mr-4">Company Logo</span><Upload><LookingForButtonComponent value="Upload file"/></Upload>
                    </div>
                    <InputFieldComponent type="password" img={mail} place="Official Mail Address"/>
                    <InputFieldComponent type="number" img={phone} place="Contact Number"/>
                    <DatePicker style={{width: "100%"}} placeholder="Establishment year"/>
                    <InputFieldComponent type="text" img={globe} place="Company website"/>
                    <div className="mt-4">
                        <Checkbox></Checkbox><span className="ml-2">I agreen to the terms and conditions of smartinterns</span>
                    </div>
                    <div className="text-center mt-3">
                        <button className="company-details-button" onClick={()=>this.changeTab(0)}>Next</button>
                    </div>
                </div>
            </>
        );
    }
}

export default CompanyFormComponent;
