import React from "react";
import LookingForButtonComponent from "../looking-for-button/looking-for-button.component";
import './looking-for-card.style.scss'
const LookingForCardComponent = ({title, details, button, classname}) =>{
    return(
        <div className={classname}>
            <h5 className="mt-3">{title}</h5>
            <p className="para-height">{details}</p>
            <div className="looking-button">
            <LookingForButtonComponent value={button}/>
            </div>
        </div>
    )
}
export default LookingForCardComponent;