import './profile-upload-resume.style.scss'
import {Upload} from "antd";

import React, {Component} from 'react';
import ApplyButtonComponent from "../../COMMON/apply-button/apply-button.component";
import UploadApplyButtonComponent from "../../COMMON/upload-and-apply-button/upload-apply-button.component";

class ProfileUploadResumeComponent extends Component {
    render() {
        return (
            <React.Fragment>
                <div className="upload text-center">
                    <Upload><UploadApplyButtonComponent type="Upload new file"/> </Upload>
                </div>
            </React.Fragment>
        );
    }
}

export default ProfileUploadResumeComponent;