
import NavigationBarComponent from "../../../components/COMMON/navigation-bar/navigation-bar.component";
import FilterJobComponent from "../../../components/CANDIDATE/filter-job/filter-job.component";
import JobListingCardComponent from "../../../components/CANDIDATE/job-listing-card/job-listing-card.component";
import './JobListing.scss'
import FooterComponent from "../../../components/COMMON/footer/footer.component";
import {Breadcrumb} from "antd";
import JobSearchBarComponent from "../../../components/CANDIDATE/job-search-bar/job-search-bar.component";
import {Link} from "react-router-dom";
import React, {Component} from 'react';
import axios from 'axios'
import {getJobs} from "../../../services/jobPostsService";
class JobListing extends Component {
    state = {
        jobs : []
    }
    async componentDidMount() {
        const {data: jobs } = await getJobs();
        const {data} = jobs;
        this.setState({jobs: data});
        console.log('Component DID MOUNT', this.props.location)

    }

    render() {
        const {jobs} = this.state;
        return (
            <React.Fragment>
                <div className="">
                    <NavigationBarComponent/>
                    <div className="job-list-main">
                        <div className="bread mt-2">
                            <Breadcrumb>
                                <Link to="/">
                                    <Breadcrumb.Item>Home</Breadcrumb.Item>
                                </Link>
                                <Breadcrumb.Item>Web Developer</Breadcrumb.Item>
                            </Breadcrumb>
                            <div className="mt-3">
                                <JobSearchBarComponent/>
                            </div>
                        </div>
                        <div className="d-flex mt-3">
                            <div className="d-none d-md-block">
                                <FilterJobComponent/>
                            </div>
                            <div className="d-block pb-5 w-100">
                                {jobs.map((job,i)=>
                                    // {/*<Link key ={job.id} to={`${this.props.match.url}/description`}>*/}
                                        <JobListingCardComponent job = {job} url = {this.props.match.url} history = {this.props.history} key ={job.id}/>
                                    // </Link>
                                )}
                            </div>
                        </div>
                    </div>
                    <FooterComponent/>
                </div>
            </React.Fragment>
        );
    }
}

export default JobListing;
