import React from "react";
import './about-explore.style.scss'

import LookingForCardComponent from "../looking-for-card/looking-for-card.component";
import AboutExploreFooterComponent from "../about-explore-footer/about-explore-footer.component";
 const AboutExploreComponent = () =>{
     return(
         <div className="about-explore">
             <div className="looking-for-container">
                 <LookingForCardComponent classname="looking-job" details="Find the right job that suits you from more than
six lakh opportunities." button="Upload resume" title="Looking for a Job?"/>
                 <LookingForCardComponent classname="looking-candidate" details="Find the perfect talent matching your company" button="Post job" title="Looking for Candidate"/>
             </div>
             <AboutExploreFooterComponent/>
         </div>
     )
 }
 export default AboutExploreComponent;