import http from './http'

const jobPostApiEndpoint = "http://localhost:5000/api/GetJob";

export function getJobs(){
    return http.get(jobPostApiEndpoint + "/"+ "all");
}
export function getJobsById(id){
    return http.get(jobPostApiEndpoint + "/"+ id )
}

export function getJobByLocation(loc_id){
    return http.get(jobPostApiEndpoint + "/" + loc_id);
}
export function getJobByCompany(company_id){
    return http.post()
}

export function jobCreate(job){
    if(job.id){
        const body = {...job};
        delete body.id;
        return http.put(jobPostApiEndpoint, body);
    }
    return http.post(jobPostApiEndpoint, job);
}

export function jobDelete(jobId) {
    return http.delete(jobPostApiEndpoint + '/' + jobId);
}