import React from 'react'
import './similar-jobs-location.style.js.scss'
const SimilarJobsLocationComponent = ({title, type}) => {
    return(
        <div className="jobs-location">
            <h6 style={{fontWeight: "600"}}>{title}</h6>
            {type.map((x, i)=><p key = {i}>{x}</p>)}
        </div>
    )
}
export default SimilarJobsLocationComponent;