import React, {Component} from 'react';
import {Col, Row} from "antd";
import ChatHeaderListComponent from "../chat-header-list/chat-header-list.component";
import ChatBoxComponent from "../chatbox/chatbox.component";

class ProfileChatComponent extends Component {
    render() {
        return (
            <React.Fragment>
                <Row>
                    <Col xs = {24} md = {8}><ChatHeaderListComponent/></Col>
                    <Col xs = {24} md = {16}><ChatBoxComponent/></Col>
                </Row>
            </React.Fragment>
        );
    }
}

export default ProfileChatComponent;