import React, {Component} from 'react';
import './application-chart.style.scss'
import {Bar} from 'react-chartjs-2'

const data = {
    labels : ["Java", "React", "Flutter", "NodeJS", "PHP"],
    datasets: [{
        label: "Dataset",
        backgroundColor: "#29E781",
        borderColor : "#29E781",
        data: [1, 10, 13, 2, 30]
    }]
}

class ApplicationChartComponent extends Component {
    render() {
        return (
            <>
                <div className="bar-container">
                    <Bar data= {data}/>
                </div>
            </>
        );
    }
}

export default ApplicationChartComponent;