import React from "react";
import SaveButtonComponent from "../../COMMON/save-buton/save-button.component";
import ApplyButtonComponent from "../../COMMON/apply-button/apply-button.component";
import {LoremIpsum} from 'react-lorem-ipsum'
import './job-details.style..scss'
import logo from '../../../assets/logo.svg'

const JobDetailsComponent = ({props}) => {

     return(
         <div className="job-details">
             <div className="w-100 d-flex"><span className="h5">{props.job_title}</span><div className="ml-auto"><ApplyButtonComponent type="Apply"/> </div></div>
             <div className="w-100 d-flex mt-2"><div className="d-flex mt-auto mb-auto"><img src = {props.company_logo} width = "50px" alt = "logo"/>&nbsp;&nbsp;<h6 className="mt-2">{props.company_name}</h6></div><div className="ml-auto"><SaveButtonComponent value="save"/></div></div>
             <p>{props.job_location}</p>

             <p>Experience: <span className="font-weight-bold">{props.experience_requires} years</span></p>
             <div className="mt-3">
             <p>Qualifications:</p>
             <LoremIpsum/>
             </div>
             <div className="mt-3">
             <p>About the job:</p>
             <LoremIpsum/>
             </div>
             <LoremIpsum/>

         </div>
     )
}
export default JobDetailsComponent;