import React, {Component} from 'react';
import {Navbar, Nav, NavDropdown, Form, FormControl, Button} from "react-bootstrap";
import logo from '../../../assets/interns.png'
import './navigation-bar.style.scss'
import UploadApplyButtonComponent from "../upload-and-apply-button/upload-apply-button.component";
import NavUserInfoComponent from "../nav-user-info/nav-user-info.component";
import {Link} from "react-router-dom";

class NavigationBarComponent extends Component {
    render() {
        return (
            <React.Fragment>
                <Navbar fixed="top" bg="light" expand="lg">
                    <Navbar.Brand><Link to = "/"><img className="mr-5" width="100px" src = {logo}/></Link></Navbar.Brand>
                    <Navbar.Toggle aria-controls = "navbar"/>
                    <Navbar.Collapse id = "navbar">
                        <Nav>
                            <Nav.Link>Around you</Nav.Link>
                            <Nav.Link>Internships</Nav.Link>
                            <Nav.Link>Post jobs</Nav.Link>
                        </Nav>
                        <Nav className = "ml-auto mr-5">
                            <UploadApplyButtonComponent type="Upload Resume"/>
                        </Nav>
                        <Nav className = "">
                            {/*<NavUserInfoComponent/>*/}
                            <Nav.Link><Link to = "/login" >Sign In</Link></Nav.Link>
                        </Nav>

                </Navbar.Collapse>
                </Navbar>
            </React.Fragment>
        );
    }
}

export default NavigationBarComponent;