import React, {Component} from 'react';
import './selected-company.style-description.scss'
import {Breadcrumb, Col, Row} from "antd";

import NavigationBarComponent from "../../../components/COMMON/navigation-bar/navigation-bar.component";
import FooterComponent from "../../../components/COMMON/footer/footer.component";
import CompanyTitleCardComponent from "../../../components/CANDIDATE/company-title-card/company-title-card.component";
import SimilarJobsLocationComponent from "../../../components/CANDIDATE/similar-jobs-location/similar-jobs-location.component";
import JobDetailsComponent from "../../../components/CANDIDATE/job-details/job-details.component";
import RequirementComponent from "../../../components/CANDIDATE/requirement/requirement.component";

import {getJobsById} from "../../../services/jobPostsService";
import {getCompany} from "../../../services/companyService";
const jobs = ['web designer', 'Web application', 'Web developer', 'Core java', 'NodeJS developer']
const location = ['Bangalore', 'Chennai', 'Goa', 'Mumbai', 'Pune'];



class SelectedCompanyDescription extends Component {
    state = {
        jobDetails : {},
        company_details : {}
    }
    async componentDidMount(){
        const {id} = this.props.match.params;
        const {data} = await getJobsById(id);
        this.setState({jobDetails: data.data[0]})
        const {data:company_data} = await getCompany(this.state.jobDetails.company_id);
        this.setState({company_details: company_data.data[0]})




    }

    render() {
        const {jobDetails, company_details} = this.state;

        return (
            <React.Fragment>
                <div>
                    <NavigationBarComponent/>
                    <div className="select">
                        <Breadcrumb>
                            <Breadcrumb.Item>Home</Breadcrumb.Item>
                            <Breadcrumb.Item>Web Developer</Breadcrumb.Item>
                            <Breadcrumb.Item>Company</Breadcrumb.Item>
                        </Breadcrumb>
                        <Row type = "flex">
                            <Col xs = {{span: 24, order: 2}} md = {{span: 6, order: 1}}>
                                <CompanyTitleCardComponent props = {this.props} company_details = { company_details }/>
                                <SimilarJobsLocationComponent title="Similar jobs for you" type={jobs}/>
                                <SimilarJobsLocationComponent title="Popular locations" type={location}/>
                            </Col>
                            <Col xs = {{span : 24, order: 1}} md = {{span : 18, order: 2}}>
                                <JobDetailsComponent props={jobDetails}/>
                                <RequirementComponent props = {jobDetails}/>
                            </Col>
                        </Row>

                    </div>
                    <FooterComponent/>
                </div>
            </React.Fragment>
        );
    }
}

export default SelectedCompanyDescription;
