import React, {Component} from 'react';
import './register-career-stage.style.scss'
import smart from '../../../assets/interns.png'
import {Col, Row} from "antd";
import bag from '../../../assets/portfolio (2).svg'
import cap from '../../../assets/black-graduation-cap-tool-of-university-student-for-head.svg'
import FreshProfComponent from "../fresher-prof/fresh-prof.component";
import {Link} from "react-router-dom";
import {useRouteMatch} from "react-router-dom";

class RegisterCareerStageComponent extends Component {

    render() {
        console.log('register:', this.props.props.match.url);
        return (
            <React.Fragment>
                <div className="main d-flex align-items-center">
                <div className="career-stage text-center w-100">
                    <img className="mt-5" src = {smart} width="100px"/>
                    <Row className = "mt-3">
                        <Col md = {6} xs = {0}></Col>
                        <Col md = {12} xs = {24}><p>Fill up the necessary details</p></Col>
                        <Col md = {6} xs = {24}><p>Already registered? Login</p></Col>
                    </Row>
                    <p className="gray-bg h6 mt-2">Select at which stage of career you are in</p>
                    <div className="d-flex justify-content-center mt-4">
                        <Link to={`${this.props.props.match.url}/Fresher`}>
                            <FreshProfComponent img={cap} classname="fresher" details="I have just graduated/ i haven't worked after graduation" title="Fresher"/>
                        </Link>
                        <Link to={`${this.props.props.match.url}/Register`}>
                            <FreshProfComponent img={bag} classname="prof" details="I have experience of at least one month" title="Experienced"/>
                        </Link>
                    </div>
                    <div className="pl-2 pr-2">
                    <h5 className="mt-3">Benefits of registering in Smart Interns</h5>
                    <p>Become searchable to get the best job opportunities matching your profile</p>
                    <p>Find Better jobs Faster with 1 click apply</p>
                    <p>Get connected with 20K+ recruiters</p>
                    </div>
                </div>
                </div>
                                                                                                  x
            </React.Fragment>
        );
    }
}

export default RegisterCareerStageComponent;
